each(map-keys(@grid-breakpoints), #(@breakpoint) {
	#media-breakpoint-up(@breakpoint, {
		@infix: breakpoint-infix(@breakpoint, @grid-breakpoints);

		each({ margin: m; padding: p; }, #(@abbrev, @prop) {
			each(@spacers, #(@length, @size) {
				.@{abbrev}@{infix}-@{size} { @{prop}: @length !important; }
				.@{abbrev}t@{infix}-@{size},
				.@{abbrev}y@{infix}-@{size} {
					@{prop}-top: @length !important;
				}
				.@{abbrev}r@{infix}-@{size},
				.@{abbrev}x@{infix}-@{size} {
					@{prop}-right: @length !important;
				}
				.@{abbrev}b@{infix}-@{size},
				.@{abbrev}y@{infix}-@{size} {
					@{prop}-bottom: @length !important;
				}
				.@{abbrev}l@{infix}-@{size},
				.@{abbrev}x@{infix}-@{size} {
					@{prop}-left: @length !important;
				}
      });

      // Negative margins (e.g., where `.mb-n1` is negative version of `.mb-1`)
      each(@spacers, #(@length, @size) {
        .@{abbrev}@{infix}-n@{size} when not (@size = 0) { @{prop}: -@length !important; }
        .@{abbrev}t@{infix}-n@{size},
        .@{abbrev}y@{infix}-n@{size} {
          & when not (@size = 0) {
            @{prop}-top: -@length !important;
          }
        }
        .@{abbrev}r@{infix}-n@{size},
        .@{abbrev}x@{infix}-n@{size} {
          & when not (@size = 0) {
            @{prop}-right: -@length !important;
          }
        }
        .@{abbrev}b@{infix}-n@{size},
        .@{abbrev}y@{infix}-n@{size} {
          & when not (@size = 0) {
            @{prop}-bottom: -@length !important;
          }
        }
        .@{abbrev}l@{infix}-n@{size},
        .@{abbrev}x@{infix}-n@{size} {
          & when not (@size = 0) {
            @{prop}-left: -@length !important;
          }
        }
      });
		});

		// Some special margin utils
		.m@{infix}-auto { margin: auto !important; }
		.mt@{infix}-auto,
		.my@{infix}-auto {
			margin-top: auto !important;
		}
		.mr@{infix}-auto,
		.mx@{infix}-auto {
			margin-right: auto !important;
		}
		.mb@{infix}-auto,
		.my@{infix}-auto {
			margin-bottom: auto !important;
		}
		.ml@{infix}-auto,
		.mx@{infix}-auto {
			margin-left: auto !important;
		}
	});
})